.custom-tab-panel {
	padding: 15px; /* Add your desired padding */
	background-color: #f9f9f9; /* Optional: Add a light background for visibility */
	border: 1px solid #ddd; /* Optional: Add a border for structure */
	border-radius: 5px; /* Optional: Smooth corners */
}

.custom-tab-panel .wp-block-editor-components-tab-panel__tab {
	margin-bottom: 10px; /* Add spacing between tabs and content */
}

.awsm-b-stack-item {
	width: 100%;
	margin-bottom: 20px;
	padding: 0 15px;
}

.awsm-b-stack-item .awsm-b-job-item {
	border-radius: var(--hz-jl-border-radius, 0px);
	border-width: var(--hz-jl-border-width, 0px);
	border-style: var(--hz-jl-border-style, none);
	border-color: var(--hz-jl-border-color, #CBCBCB);

	padding-top: var(--hz-jl-padding-top, 30px);
	padding-right: var(--hz-jl-padding-right, 30px);
	padding-bottom: var(--hz-jl-padding-bottom, 30px);
	padding-left: var(--hz-jl-padding-left, 30px);

	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	gap: 10px;
}

.awsm-b-stack-left-col {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;

	.awsm-job-featured-image {
		max-width: 55px;
		border-radius: 10px
	}

	.awsm-job-specification-wrapper {
		display: flex;
		gap: 15px 30px;
		align-items: center;
		flex-wrap: wrap;
	}

}

.filters-button {
	margin-bottom: 16px;
}

.awsm-selectric {
	//border-radius: 5px;
	border-radius: var(--hz-ls-border-radius, 5px) !important;
